import React from 'react';
import ReactDOM from 'react-dom';
import { Navbar } from './navbar';


export class HomePage extends React.Component{
    constructor(){
        super()
    }


    render(){
        return(
            <div>
                <div>
                   <Navbar />
                   <Header />
                </div>
                <div className="Wrapper">
                    
                    <AboutUs />
                    <OurTours />
                    <ChooseYourTour />
                    <OurPros />
                    <Equipment />
                    <LastForm />
                </div>
            </div>
        )
    }
}

//Header block
class Header extends React.Component{
    constructor(){
        super()
    }


    render(){
        return(
            <div id="Header">
                <div className="Wrapper">
                   <h1 className='Header-headline1'>LUCKY TRAVEL</h1>
                   <h2 className='Header-headline2'>
                        Лучшие<br />
                        предложения<br />
                        для вашего активного<br />
                        отдыха
                    </h2>
                    <div className='Header-line'></div>
                    <div className="Header-form-ourUs"><a href="#">УЗНАТЬ БОЛЬШЕ</a></div>
                    <div>
                        <form action='telegram.php' method='POST ' className="Header-form-box1">
                            <div className="Header-form">
                                <legend className="Header-form-legend">Оставьте заявку для подробной информации</legend>
                            </div>
                            <div className="Header-form-box2">
                                <div>
                                    <input name="user_name" className="Header-form-input1" type="text" placeholder="Ваше имя" />
                                </div>
                                <div>
                                    <input name="user_phone" className="Header-form-input2" type="text" placeholder="Ваш номер телефона" />
                                </div>
                                <div>
                                    <button type='submit' className="Header-form-button">Отправить!</button>
                                </div>
                            </div>
                        </form>
                    </div>

                </div>
            </div>
            
        )
    }
}
